/*********** CUSTOM CHECKBOX *************/
.form_custom_label {
  display: flex;
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  font-size: 13px;
  user-select: none;
  color: #1d82f5;
  color: #28313c;
  align-items: center;
  justify-content: center;
}

.form_custom_label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  border: solid 2px #1d82f5;
  background-color: #fff;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.form_custom_label.checkmark-20 {
  padding-top: 3px;
}
.form_custom_label.checkmark-20 .checkmark {
  height: 20px;
  width: 20px;
  border-radius: 4px;
}
.form_custom_label.checkmark-20 .checkmark:after {
  left: 5.5px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.form_custom_label.checkmark-15 {
  padding-top: 1px;
}
.form_custom_label.checkmark-15 .checkmark {
  height: 15px;
  width: 15px;
  border-radius: 3px;
}
.form_custom_label.checkmark-15 .checkmark:after {
  left: 3.5px;
  top: 0.5px;
  width: 5px;
  height: 8px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.form_custom_label input:checked ~ .checkmark {
  background-color: #1d82f5;
}

.form_custom_label input:checked ~ .checkmark:after {
  display: block;
}

.custom-control-input:checked ~ .custom-control-label::before {
  color: #fff;
  border-color: #1d82f5;
  background-color: #1d82f5;
}

.custom-control-input:focus ~ .custom-control-label::before {
  box-shadow: 0 0 0 0.2rem rgba(29, 130, 245, 0.1607843137);
}

